.App {
  text-align: center;
}
.applicationFormTitle {
}

.formLayout {
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.errorMessage {
  color: red;
}

.finePrint {
  font-size: smaller;
  font-style: italic;
}

.stepButtons {
  margin: 4px;
}

.prevImage {
  max-width: 200px;
  height: auto;
}
.color-nav {
  background-color: rgba(209, 209, 209, 0.527);
  z-index: 10;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.construction {
  color:#EEEEEE;
  text-shadow: 1px 1px 2px #000000;
  margin-top: 60px;
  font-size:24pt;
  right: 80px;
}

@media only screen and (min-width: 200px) and (max-width: 670px) {
  .navBarLogo {
    max-width:200px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
